<template>
  <div>
    <div id="dplayer" ref="player" class="play-root" />
    <VideoVip :video="video" ref="VideoVip" />
  </div>
</template>

<script>
import apiUser from "@/api/user";
import VideoVip from "@/components/global/VideoVip";
//import auth from "@/plugins/auth";
export default {
  name: "PlayerVideo",
  components: {
    VideoVip,
  },
  data() {
    return {
      dp: null,
      user: "",
    };
  },
  props: {
    needVip: {
      type: Boolean,
      default: true,
    },
    video: {
      type: Object,
      default: () => {
        return {
          source: "",
          url: "",
          _id: "",
        };
      },
    },
  },
  computed: {
    isVip() {
      return this.$store.state.tradeNo;
    },
  },
  methods: {
    loadVideo() {
      console.log(this.video);
      // eslint-disable-next-line no-undef
      window.dp = this.dp = new DPlayer({
        container: this.$refs.player,
        autoplay: false,
        video: {
          ...this.video,
          url: `${this.video.url}`,
        },
      });
      this.dp.on("timeupdate", () => {
        // if (this.dp.video.currentTime > 0 && !this.isVip) {
        //   this.dp.pause();
        //   this.dp.fullScreen.cancel("web");
        //   if (!this.$refs.VideoVip.dialogVisible) {
        //     this.$refs.VideoVip.dialogVisible = true;
        //   }
        // }
      });
    },
    async queryApplyInfo() {
      const trade_no = this.isVip;
      const [err] = await apiUser.vaidOrder({
        trade_no,
      });
      if (err) {
        this.$store.commit("clearTradeNo");
        return;
      }
    },
  },
  mounted() {
    this.$nextTick(() => {
      this.loadVideo();
    });
  },
  beforeDestroy() {
    this.dp && this.dp.destroy();
  },
  created() {
    this.queryApplyInfo();
  },
};
</script>
