<template>
  <el-dialog
    :title="VIP会员"
    :modal-append-to-body="true"
    :append-to-body="true"
    :visible.sync="dialogVisible"
    :close-on-click-modal="false"
    :width="isMobile ? '98%' : '40%'"
  >
    <ul
      class="vip-list"
      v-for="(item, idx) in vipList"
      :key="`${idx}`"
      @click="curIndex = idx"
    >
      <li>
        <h2>{{ item.name }}</h2>
      </li>
      <li>￥{{ item.price }}</li>
      <li>{{ item.desc }}</li>
      <li>
        <el-button
          type="primary"
          class="vip-buy"
          size="small"
          @click="CreateOrder"
        >
          <img width="26" src="@/assets/alilogo_small.png" />
          支付宝开通</el-button
        >
      </li>
    </ul>
    <form
      ref="myForm"
      action="https://pay.kuangtianxia.cn/submit.php"
      v-if="order.pid"
    >
      <input
        type="hidden"
        v-for="(item, idx) in order"
        :key="`${idx}`"
        :name="idx"
        :value="item"
      />
    </form>
  </el-dialog>
</template>

<script>
//import { uid } from "uid";
import auth from "@/plugins/auth";
import apiUser from "@/api/user";
export default {
  name: "videoVip",
  props: {
    video: {
      type: Object,
      default: () => {
        return {
          source: "",
          url: "",
          _id: "",
        };
      },
    },
  },
  data() {
    return {
      loading: false,
      dialogVisible: false,
      curIndex: 0,
      order: {},
      vipList: [
        {
          name: "月卡VIP",
          price: 88,
          desc: "特惠！31天,随心所欲观看视频不受限制",
        },
      ],
    };
  },
  computed: {
    curItem() {
      return this.vipList[this.curIndex];
    },
    isMobile() {
      return this.$store.state.isMobile;
    },
  },
  methods: {
    async CreateOrder() {
      // eslint-disable-next-line no-unused-vars
      const [err, res] = await apiUser.CreateOrder({
        type: "alipay",
        price: this.curItem.price,
      });
      if (err) return;
      this.order = res;
      auth.setToken("buyId", this.video._id);
      this.$nextTick(() => {
        this.$refs.myForm.submit();
      });
    },
  },
};
</script>
